@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "Unbounded";
  src: local("Unbounded"),
    url("./fonts/Unbounded/static/Unbounded-Regular.ttf") format("truetype");
  font-weight: regular;
}

@font-face {
  font-family: "Unbounded";
  src: local("Unbounded"),
    url("./fonts/Unbounded/static/Unbounded-Bold.ttf") format("truetype");
  font-weight: bold;
}

html {
  height: -webkit-fill-available;
}

body {
  margin: 0;
  display: flex;
  background: rgb(26, 26, 26);
  box-sizing: border-box;
  color: white;
  font-size: 16px;
  font-family: "Unbounded", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100dvh;
  /* max-height: 100vh; */
  max-height: 100dvh;
}

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  width: 100vw;
  height: 100vh;
  min-height: 100vh;
  /* max-height: 100vh; */
  max-height: 100vh;
  min-height: -webkit-fill-available;
  max-height: -webkit-fill-available;
  /* display: grid;
  flex-direction: column;
  grid-template-columns: 1fr;
  grid-template-rows: 106px 1fr; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.barlow-semi-condensed-medium {
  font-family: "Barlow Semi Condensed", sans-serif;
  font-weight: 500;
  font-style: normal;
}

.barlow-semi-condensed-bold {
  font-family: "Barlow Semi Condensed", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.barlow-semi-condensed-black {
  font-family: "Barlow Semi Condensed", sans-serif;
  font-weight: 900;
  font-style: normal;
}

.trapeciya {
  transform: perspective(100px) rotateX(30deg);
  box-shadow: inset 0 15px 30px rgba(0, 0, 0, 1);
}

@media screen and (min-width: 640px) {
  .trapeciya {
    transform: perspective(200px) rotateX(30deg);
  }
}

@media screen and (min-width: 768px) {
  .trapeciya {
    transform: perspective(100px) rotateX(30deg);
  }
}

@media screen and (min-width: 1024px) {
  .trapeciya {
    transform: perspective(200px) rotateX(30deg);
  }
}